<template>
  <div>
    <div v-if="$store.state.user" class="nes-container is-dark with-title">
      <p class="title">Feed & Claim</p>
      <div
        style="background-color: #212529; padding: 1rem"
        class="nes-field is-inline"
      >
        <label for="dark_field" style="color: #fff">Pet IDs:</label>
        <input v-model="pets"
          type="text"
          id="dark_field"
          class="nes-input is-dark"
          placeholder="[1,920,50,900]"
        />
      </div>
      <div style="background-color:#212529; padding: 1rem 1.2rem 1rem 1rem;width:calc(100% + 8px)">
        <label for="dark_select" style="color:#fff">Food</label>
        <div class="nes-select is-dark">
          <select required id="dark_select" v-model="food">
            <option value="" disabled selected hidden>Select...</option>
            <option v-for="option in $store.state.food" v-bind:key="option.name" :value="option.id">{{option.name}} - {{option.price}} Cudl</option>
          </select>
        </div>
      </div>
      <div style="width: 100%; text-align: right">
        <button type="button" :class="'nes-btn is-primary ' + (disabled ? 'is-disabled' : '')" @click="execute">Send Transaction</button>
      </div>
    </div>
    <connect v-else style="text-align: center; margin-top: 50px"></connect>
  </div>
</template>
<script>
import Connect from '@/components/Connect.vue'
export default {
  name: "SelfServe",
  components: {
    Connect
  },
  data() {
      return {
        pets: null,
        food: null,
        loading: false,
      }
  },
  methods: {
    async execute(){
      this.loading = true
      try{
        await this.$store.dispatch("feedMultiple", {pets: JSON.parse(this.pets), food: this.food})
      }
      catch(err){
        console.error(err)
        alert(err["message"])
      }
      this.loading = false
    }
  },
  computed: {
    disabled(){ 
      if(!this.pets){
        return true
      }
      if(!this.food){
        return true
      }
      if(this.loading){
        return true
      }
    }
  },
  async mounted(){
    this.pets = await this.$store.dispatch("getPetIds", this.$store.state.user)
  }
}
</script>