<template>
  <div>
    <button v-if="!$store.state.user" type="button" class="nes-btn is-success" @click="Connect()">Connect</button>
    <div v-else>
        <img class="nes-avatar is-rounded is-large" alt="Ethereum blockies avatar" :src="blocky" :title="$store.state.user" style="image-rendering: pixelated;" @click="toggle()">
    </div>
  </div>
</template>
<script>
import makeBlockie from 'ethereum-blockies-base64';


export default {
    name: "Connect",
    data(){
        return {
            dialog: false,
        }
    }, 
    methods: {
        async Connect(){
            this.$store.dispatch("Connect")
        },
        async Disconnect(){
            this.$store.dispatch("Connect")
        },
        toggle(){
            let modal = document.getElementById('dialog-default');  
            this.dialog = this.dialog ? false : true
            this.dialog ? modal.showModal() : modal.close();

        }
    },
    computed: {
        signer(){
            return this.$store.state.signer
        },
        blocky(){
            let user = this.$store.state.user
            if(user){
                return makeBlockie(user);
            }
            return ""
        }
    }
}

</script>