import { createStore } from 'vuex'
import Web3Modal from "web3modal";
import WalletConnectProvider from "@walletconnect/web3-provider";
import { markRaw } from 'vue';
const { providers, Wallet, ethers, BigNumber } = require("ethers");
const CHAIN_ID = 42161
const RPC = "https://arb1.arbitrum.io/rpc"
const NATIVE_TOKEN = "0xEeeeeEeeeEeEeeEeEeEeeEEEeeeeEeeeeeeeEEeE"

let taskTreasury = {address: "0x527a819db1eb0e34426297b03bae11f2f8b3a19e", abi: require("../assets/ABI/TaskTreasury.json")}
let pokeMe = {address: "0xb3f5503f93d5ef84b06993a1975b9d21b962892f", abi: require("../assets/ABI/PokeMe.json")}
export default createStore({
  state: {
    web3Modal: null,
    serviceFee: null,
    provider: null,
    signer: null,
    user: null,  
    petIDs: [],
    foodIDs: [],
    food: [
      {name: "Bananuman", price: 0.5, id: 1}, 
      {name: "Catnip", price: 0.6, id:2}, 
      {name: "Cucombre", price: 2, id: 3}, 
      {name: "Moon Milk", price: 5, id: 4}, 
      {name: "Thic Duck", price: 0.8, id: 5}, 
      {name: "Tuna", price: 10, id: 5}
    ],
    contract: markRaw(new ethers.Contract("0x503cdba19c484fc4aec22f0e8f3dc37a5c327c27", require("../../../ABI/Babysitter.json"), new providers.JsonRpcProvider({ url: RPC }, CHAIN_ID))),
    send: function(tx, provider){
      provider.send('eth_sendTransaction',[{from: tx.from, to: tx.to, data: tx["data"], value: tx["value"]}])
    }
  },
  mutations: {
    setWeb3Modal(state, payload){
      state.web3Modal = markRaw(payload)
    },
    setSigner(state, payload){
      state.signer = markRaw(payload)
    },
    setProvider(state, payload){
      state.provider = markRaw(payload)
    },
    setServiceFee(state, payload){
      state.serviceFee = payload
    },
    setUser(state, payload){
      state.user = payload
    }, 
    setContract(state, payload){
      state.contract = payload
    }
  },
  actions: {
    toEth({state},wei){
      return ethers.utils.formatEther(wei.toString())
    },
    async getServiceFee({state, commit}){
      let percentage = await state.contract.percentage()
      commit("setServiceFee", percentage)
      return percentage
    },
    /**
     * 
     * @param {Vuex.state} param0 
     * @param {[Number]} pets 
     * @param {[Number]} foods 
     */
    async feedMultiple({ state },{pets, food}){
      if(typeof(food) == 'string'){
        let id = state.food.filter(x => x.name == food)["id"]
        food = Array(pets.length).fill(id)
      }
      if(typeof(food) == 'number'){
        food = Array(pets.length).fill(food)
      }
      let tx = await state.contract.populateTransaction.FeedMultiple(pets, food)
      state.send(tx, state.provider)
    },
    async checkRewards({state}, parent){
      return await state.contract.pendingRewards(parent)
    },
    async claimRewards({state}, parent){
      let can = await state.contract.pendingRewards(parent)
      if(can.gt(BigNumber.from(0))) {
        let tx = await state.contract.populateTransaction.Claim(parent)
        state.send(tx, state.provider)
      }
      else {
        throw new Error("Nothing to claim")
      }

    },
    async getPetIds({state}, parent){
      let pets = fetch("https://api.nft20.io/cudl/owner/" + parent).then(res => res.json())
      return pets["petsOwned"]
    },
    async gasBalance({state}, parent){
      let treasury = new ethers.Contract(taskTreasury.address, taskTreasury.abi, state.signer)
      return await treasury.userTokenBalance(parent, NATIVE_TOKEN)
    },
    async depositBalance({state}, amount){
      if(amount > 0){
        let treasury = new ethers.Contract(taskTreasury.address, taskTreasury.abi, state.signer)
        //let tx = await treasury.populateTransaction.depositFunds(ethers.utils.parseEther(amount.toString()), state.user, NATIVE_TOKEN, 0)
        let tx = await treasury.populateTransaction.depositFunds(state.user, NATIVE_TOKEN, ethers.utils.parseEther(amount.toString()))
        tx.value = ethers.utils.parseEther(amount.toString()).toHexString()
        state.send(tx, state.provider)
      }
    },
    async withdrawBalance({state}, amount){
      if(amount > 0) {
        let treasury = new ethers.Contract(taskTreasury.address, taskTreasury.abi, state.signer)
        let tx = await treasury.populateTransaction.withdrawFunds(state.user, NATIVE_TOKEN, ethers.utils.parseEther(amount.toString()).toString())
        state.send(tx, state.provider)
      }
    },
    async automate({state}, {pets, food}){
      if(typeof(food) == 'string'){
        let id = state.food.filter(x => x.name == food)["id"]
        food = Array(pets.length).fill(id)
      }
      if(typeof(food) == 'number'){
        food = Array(pets.length).fill(food)
      }
      let scheduler = new ethers.Contract(pokeMe.address, pokeMe.abi, state.signer)
      // start time, interval in seconds, contract address to look at, function selector to look at, which data should be used to check, which token for payment, should funds be paid from treasury balance
      let calldata = await state.contract.interface.encodeFunctionData("FeedMultiple",[pets, food])
      let selector = await scheduler.getSelector("FeedMultiple(uint256,uint256)")
      let resolver = new ethers.Contract("0x95f4538c3950ce0ef5821f2049ae2ac5ccade68d", require("../assets/ABI/Forwarder.json"), state.signer)
      calldata = resolver.interface.encodeFunctionData("checker",[calldata])
      //let resolver_data = ""
      let tx = await scheduler.populateTransaction.createTimedTask(0, 86400, state.contract.address, "0xa908772c", resolver.address, calldata, NATIVE_TOKEN, true)
      state.send(tx, state.provider)
    },
    async getAutomations({state}, user){
      let scheduler = new ethers.Contract(pokeMe.address, pokeMe.abi, state.signer)
      let ids = await scheduler.getTaskIdsByUser(user)
      return ids
    },
    async cancelAutomation({state}, id){
      debugger
      let scheduler = new ethers.Contract(pokeMe.address, pokeMe.abi, state.signer)
      let tx = await scheduler.populateTransaction.cancelTask(id)
      state.send(tx, state.provider)
    },
    async Connect({commit, state}){
      const providerOptions = {
          walletconnect: {
              package: WalletConnectProvider, // required
              options: {
              infuraId: "INFURA_ID" // required
              }
          }
      };
      const web3Modal = new Web3Modal({
      network: "arbitrum", // optional
      cacheProvider: true, // optional
      providerOptions // required
      });
      
      const provider = new providers.Web3Provider(await web3Modal.connect());
      const signer = provider.getSigner();
      commit('setSigner', signer)
      commit('setUser', await signer.getAddress())
      commit('setProvider', provider)
      commit('setWeb3Modal', web3Modal)
      commit('setContract', markRaw(new ethers.Contract("0x503cdba19c484fc4aec22f0e8f3dc37a5c327c27", require("../../../ABI/Babysitter.json"), signer)))
  },
  async Disconnect({state, commit}){
      await state.web3Modal.clearCachedProvider();
      commit('setSigner', null)
      commit('setProvider', null)
      window.location.reload();
  }
  },
  modules: {
  }
})
