<template>
  <div class="home" style="text-align: left">
    <div class="section">
      <h2>Automation</h2>
      <p>Feed and claim all your pets once a day. <u>{{serviceFee}}% Cudl service fee. </u></p>
      <automation v-if="$store.state.user"></automation>
      <connect v-else style="text-align: center; margin-top: 50px"></connect>

    </div>
    <div class="section">
      <h2>Self Serve</h2>
      <p>Feed and claim all your pets at once. <u>{{serviceFee}}% Cudl service fee.</u></p>
      <self-serve v-if="$store.state.user"></self-serve>
      <connect v-else style="text-align: center; margin-top: 50px"></connect>
    </div>

    <div class="section">
      <h2>How it works</h2>
      <div class="lists">
        <ul class="nes-list is-disc">
          <li>You must approve this contract ({{contract}}) as a caretaker for every pet you want to use this service with. You may do so <a href="https://cudl.finance/care-taker">here</a></li>
          <br>
          <li>The contract will attempt to feed and claim Cudl for one or many pets in a single transaction. Therefore, any failure to feed or claim will fail the entire transaction leaving no pets fed/claimed.</li>
          <br>
          <li>It will fail to feed a pet if there is no Cudl to claim, so <u>do not claim if you want to use automation.</u></li>
          <br>
