<script>
// @ is an alias to /src
import SelfServe from '@/components/SelfServe.vue'
import HelloWorld from '@/components/HelloWorld.vue'
import Automation from '@/components/Automation.vue'
import Connect from "@/components/Connect.vue";


export default {
  name: 'Home',
  data(){
    return {
      
    }
  },
  components: {
    HelloWorld,
    Connect,
    SelfServe,
    Automation
  },
  computed: {
    serviceFee(){
      if(this.$store.state.serviceFee){
        return (this.$store.state.serviceFee / 100).toFixed(2);
      }
      else {
        return "..."
      }
    },
    parentPercentage(){
      if(this.serviceFee != "..."){
        return (100 - this.serviceFee).toFixed(2)
      }
      else{
        return "..."
      }
    },
    contract(){
      return this.$store.state.contract["address"]
    }
  },
  mounted(){
    this.$store.dispatch("getServiceFee")
  }
}
