<template>
  <div>

    <div class="nes-container is-dark">
      <div class="nes-container is-dark with-title">
        <p class="title">Gas Fund</p> 
        <p>Every tx costs a flat 0.0005 ETH</p>
        <label for="dark_field" style="color: #fff">Amount (ETH)</label>
        <input
          type="number"
          id="dark_field"
          class="nes-input is-dark"
          placeholder=""
          v-model="amount"
        />
        <br />
        <br>
        <button
          style="float: right; display: inline-block"
          type="button"
          class="nes-btn"
          @click="getCredits()"
        >
          Check Balance
        </button>

        <button
          style="float: right; display: inline-block"
          type="button"
          :class="'nes-btn is-secondary ' + (disabledGasFund ? 'is-disabled' : '')"
          @click="withdraw()"
        >
          Withdraw
        </button>
        <button
          style="float: right; display: inline-block"
          type="button"
          :class="'nes-btn is-primary ' + (disabledGasFund ? 'is-disabled' : '')"
          @click="deposit()"
        >
          Deposit
        </button>
        <br>
        <br />
        <br />
        <p v-if="credits" style="text-align: right; float right">{{credits}} ETH</p>
      </div>
      <div class="nes-container is-dark with-title">
        <p class="title">Automate</p>

        <div
          style="background-color: #212529; padding: 1rem"
          class="nes-field is-inline"
        >
          <label for="dark_field" style="color: #fff">Pet IDs:</label>
          <input
            v-model="pets"
            type="text"
            id="dark_field"
            class="nes-input is-dark"
            placeholder="[1,920,50,900]"
          />
        </div>
        <div
          style="
            background-color: #212529;
            padding: 1rem 1.2rem 1rem 1rem;
            width: calc(100% + 8px);
          "
        >
          <label for="dark_select" style="color: #fff">Food</label>
          <div class="nes-select is-dark">
            <select required id="dark_select" v-model="food">
              <option value="" disabled selected hidden>Select...</option>
              <option
                v-for="option in $store.state.food"
                v-bind:key="option.name"
                :value="option.id"
              >
                {{ option.name }} - {{ option.price }} Cudl
              </option>
            </select>
          </div>
        </div>
        <div style="width: 100%; text-align: right">
          <button type="button" :class="'nes-btn is-primary ' + (disabledAutomate ? 'is-disabled' : '')" @click="automate()">
            New Automation
          </button>
        </div>
        <br />
      </div>
      <div class="nes-container is-dark with-title" v-if="list.length > 0">
        <p class="title">Manage Automations</p>
        <div class="nes-table-responsive" style="overflow: hidden">
          <table class="nes-table is-bordered is-dark" style="width: 100%">
            <thead>
              <tr>
                <th>ID</th>
                <th>Cancel</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="automation in list" :key="automation">
                <td>{{automation}}</td>
                <td>
                  <button
                    type="button"
                    :class="'nes-btn is-error ' + (disabledCancel ? 'is-disabled' : '')"
                    @click="cancel(automation)"
                  >
                    X
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  name: "Automation",
  data() {
    return {
      credits: null,
      loadingGasFund: false,
      loadingAutomate: false,
      loadingCancelAutomation: false,
      amount: 0.0005,
      pets: null,
      food: null,
      list: [],
    };
  },
  methods: {
    async getCredits() {
      let result = await this.$store.dispatch(
        "gasBalance",
        this.$store.state.user
      );
      this.credits = await this.$store.dispatch("toEth",result);
    },
    async deposit(){
      this.loadingGasFund = true;
      try {
        await this.$store.dispatch("depositBalance", this.amount)
      }
      catch(err) {
        alert(err["message"])
      }
      this.loadingGasFund = false
    },
    async withdraw(){
      this.loadingGasFund = true;
      try{
        await this.$store.dispatch("withdrawBalance", this.amount)
      }
      catch(err) {
        alert(err["message"])
      }
      this.loadingGasFund = false
    },
    async automate(){
      this.loadingAutomate = true;
      try{
        await this.$store.dispatch("automate", {pets: JSON.parse(this.pets), food: this.food})
      }
      catch(err) {
        alert(err["message"])
      }
      this.loadingAutomate = false;
    },
    async cancel(id){
      this.loadingCancelAutomation = true;
      try{
        await this.$store.dispatch("cancelAutomation", id)
      }
      catch(err) {
        alert(err["message"])
      }
      this.loadingCancelAutomation = false;
    }
  },
  computed: {
    disabledGasFund(){
      if(this.amount > 0 && this.loadingGasFund == false){
        return false
      }
      return true
    },
    disabledAutomate(){
      if(this.pets && this.food && this.loadingAutomate == false){
        return false
      }
      return true
    },
    disabledCancel(){
      if(this.loadingCancelAutomation == false){
        return false
      }
      return true
    }
  },
  async mounted() {
    this.pets = await this.$store.dispatch("getPetIds", this.$store.state.user)
    this.list = await this.$store.dispatch("getAutomations", this.$store.state.user)
  },
};
</script>